        <PopoverContent :width="350">
          <VTypography font-weight="medium"> </VTypography>
        </PopoverContent>
      </template></TypographyWithInfo
    >

    <div class="technology-form__files" v-if="files.length > 0">
      <TechnologyFileItem
        :file="file"
        :key="file.name"
        v-for="(file, index) in files"
        @delete="onDeleteClick(index)"
        class="technology-form__file-item"
      />
    </div>

    <VTypography class="technology-form__upload-files-title"
      > </VTypography
    >

    <FilePicker :loading="loading" @change="onFilesSelected" />
  </FormsContainer>
